section#about {
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding: 20px;
    min-height: 100vh;

    h1 {
        display: inline-block;
        background: linear-gradient(135deg, #0d6efd, hotpink);
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 50px;
        margin-top: 75px;
    }


    hr {
        margin: 20px 0;
        background-color: #fff;
        border-radius: 10px;
        height: 4px;
        width: 50px;
        border: none;
        margin: 0 auto;
    }

    div.content {
        display: flex;
        gap: 50px;
        flex-direction: column;
        max-width: 800px;
    }

    div.overview {
        color: #fff;
    }

    div.listsWrapper {
        color: #fff;

        .lists {
            display: flex;
            gap: 20px;
            justify-content: space-evenly;
            flex-wrap: wrap;
        }
    }
}