section#gallery {
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding: 20px;
    min-height: 100vh;

    h1 {
        display: inline-block;
        background: linear-gradient(135deg, #0d6efd, hotpink);
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 50px;
        margin-top: 75px;
    }

    hr {
        margin: 20px 0;
        background-color: #fff;
        border-radius: 10px;
        height: 4px;
        width: 50px;
        border: none;
        margin: 0 auto;
    }

    div.content {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-rows: 1fr;
        flex-wrap: wrap;
        max-width: 800px;
        gap: 20px;

        a.imgWrapper {
            grid-column: span 1;
            aspect-ratio: 1;
            border-radius: 10px;
            overflow: hidden;
            position: relative;
            cursor: pointer;

            div.text {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                background: rgba(0, 0, 0, 0.65);
                display: flex;
                align-items: center;
                justify-content: center;
                color: #fff;
                flex-direction: column;
                opacity: 0;
                transition: 0.3s ease;
                text-align: center;

                &.hovered {
                    opacity: 1;
                }
            }

            img {
                height: 100%;
                width: 100%;
                object-fit: cover;
                object-position: center;
            }
        }
    }
}
@media (max-width: 600px) {
    section#gallery {
        div.content {
            a.imgWrapper {
                grid-column: span 2 !important;
            }
        }
    }
}
@media (max-width: 300px) {
    section#gallery {
        div.content {
            a.imgWrapper {
                grid-column: span 4 !important;
            }
        }
    }
}