section#interests {
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding: 20px;
    min-height: 100vh;

    h1 {
        display: inline-block;
        background: linear-gradient(135deg, #0d6efd, hotpink);
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 50px;
        margin-top: 75px;
    }

    hr {
        margin: 20px 0;
        background-color: #fff;
        border-radius: 10px;
        height: 4px;
        width: 50px;
        border: none;
        margin: 0 auto;
    }

    div.content {
        display: flex;
        gap: 50px;
        flex-direction: column;
        max-width: 800px;
        width: 100%;

        div.section {
            color: #fff;
            display: flex;
            align-items: center;

            .col {
                width: 50%;
                display: flex;
                align-items: center;
                justify-content: center;

                img {
                    max-width: 250px;
                }

                p {
                    font-weight: bold;
                }
            }

        }
    }
}
@media (max-width: 700px) {
    section#interests {
        div.content {
            gap: 50px;

            div.section {
                flex-direction: column;
                gap: 20px;

                div.col {
                    width: 100%;
                }
            }
        }
    }
}