aside {
    width: 300px;
    height: 100vh;
    background-color: red;
    position: fixed;
    top: 0;
    left: -300px;
    z-index: 4;
    padding: 20px;
    transition: 0.3s ease;
    font-family: 'Montserrat';
    background-color: #212529;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-y: scroll;

    &.open {
        left: 0;
    }

    div.header {
        display: flex !important;
        justify-content: space-between !important;

        a.logo {
            text-decoration: none;
            color: #fff;
            display: flex !important;
            align-items: center !important;
            gap: 10px;
    
            img {
                width: 35px;
                aspect-ratio: 1;
                border-radius: 50%;
            }
    
            h1 {
                font-size: 25px;
            }
        }

        button {
            background-color: transparent;
            transition: 0.3s ease;
            border: none;
            height: 50px;
            width: 50px;
            border-radius: 50%;
            display: flex !important;
            align-items: center !important;
            justify-content: center !important;
            cursor: pointer;

            &:hover {
                background-color: rgba(13, 110, 253, 0.1);
            }
        }
    }
    hr {
        margin: 0 10px;
        height: 1px;
        background-color: rgba(255, 255, 255, 0.05);
        border: none;
    }
    div.content {
        display: flex !important;
        flex-direction: column !important;
        justify-content: space-between !important;
        flex: 1 !important;
        gap: 15px;

        &.mobile {
            justify-content: flex-start !important;
        }

        div.links {
            display: flex !important;
            flex-direction: column !important;
            gap: 15px;
    
            a.link {
                display: flex !important;
                align-items: center !important;
                gap: 10px;
                text-decoration: none;
                color: #fff;
                font-weight: bold;
                padding: 10px 14px;
                transition: 0.3s ease;
                border-radius: 7px;
    
                &:hover {
                    background-color: rgba(13, 110, 253, 0.15);
                }

                &.active {
                    background-color: rgba(13, 110, 253, 0.15);
                    color: #0d6efd;
                }
            }
        }

        a.contact {
            text-decoration: none;
            color: #fff;
            display: flex !important;
            gap: 5px;
            align-items: center !important;
            padding: 10px 14px;
            border-radius: 7px;
            font-weight: bold;
            transition: 0.3s ease;
            background: linear-gradient(135deg, #0d6efd, #30558d);
        }
    }
}
@media (max-width: 400px) {
    aside {
        left: -100%;
        width: 100%;
    }
}