nav {
    position: fixed !important;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 40px;
    font-family: 'Montserrat';
    position: relative;
    z-index: 2;
    transition: 0.3s ease;

    &.scrolled {
        background-color: #212529;
        box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2);
    }

    a.logo {
        text-decoration: none;
        color: #fff;
        display: flex;
        align-items: center;
        gap: 10px;

        img {
            width: 35px;
            aspect-ratio: 1;
            border-radius: 50%;
        }

        h1 {
            font-size: 25px;
        }
    }

    div.links {
        position: absolute;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        left: 50%;
        display: flex;
        gap: 20px;

        a.link {
            text-decoration: none;
            color: #fff;
            display: flex;
            gap: 5px;
            align-items: center;
            padding: 10px 14px;
            border-radius: 7px;
            font-weight: bold;
            transition: 0.3s ease;

            &:hover {
                background-color: rgba(13, 110, 253, 0.15);
            }

            &.active {
                background-color: rgba(13, 110, 253, 0.15);
                color: #0d6efd;
            }
        }
    }

    a.contact {
        text-decoration: none;
        color: #fff;
        display: flex;
        gap: 5px;
        align-items: center;
        padding: 10px 14px;
        border-radius: 7px;
        font-weight: bold;
        transition: 0.3s ease;
        background: linear-gradient(135deg, #0d6efd, #30558d);
    }

    button.toggler {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        display: none;
        align-items: center;
        justify-content: center;
        border: none;
        background-color: transparent;
        cursor: pointer;
        transition: 0.3s ease;

        &:hover {
            background-color: rgba(13, 110, 253, 0.1);
        }
    }
}

@media (max-width: 1200px) {
    nav { 
        div.links, a.contact {
            display: none;
        }
        button.toggler {
            display: flex;
        }
    }
}