section#contact {
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding: 20px;
    min-height: 100vh;

    h1 {
        display: inline-block;
        background: linear-gradient(135deg, #0d6efd, hotpink);
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 50px;
        margin-top: 75px;
    }

    hr {
        margin: 20px 0;
        background-color: #fff;
        border-radius: 10px;
        height: 4px;
        width: 50px;
        border: none;
        margin: 0 auto;
    }

    div.content {
        max-width: 600px;
        width: 100%;
        gap: 30px;
        display: flex;
        flex-direction: column;
    }

    form {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;

        input, textarea {
            padding: 10px;
            border-radius: 7px;
            border: none;
            font-family: 'Montserrat';
            transition: 0.3s ease;

            &:focus {
                border: none !important;
                box-shadow: -5px -5px 20px 2px rgb(13, 110, 253, 0.5), 5px 5px 20px 2px rgb(255, 105, 180, 0.5) ;
                outline: none !important;
            }
        }

        input {
            grid-column: span 1;
        }
        textarea {
            grid-column: span 2;
            width: 100% !important;
            min-height: 150px;
            max-height: 300px;
        }

        button {
            grid-column: span 2;
            border-radius: 100px;
            padding: 10px;
            font-family: 'Montserrat';
            color: #fff;
            font-weight: bold;
            border: none;
            background: linear-gradient(135deg, #0d6efd, hotpink);
            cursor: pointer;

            &:hover {
                background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), linear-gradient(135deg, #0d6efd, hotpink);
            }
        }
    }

    div.socials {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        row-gap: 20px;

        div.social {
            grid-column: span 2;
            display: flex;
            flex-direction: column;
            align-items: center;
            color: #fff;
            gap: 5px;
            font-weight: bold;

            a {
                color: #fff;
                text-decoration: none;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}